img {
  width: 100%;
  opacity: 1;
}

.bottomleft {
  position: absolute;
  bottom: 70px;
  left: 18px;
  font-size: 18px;
  font-weight: 800;
}
#footisfoot {
  position: static;
  bottom: 0px;
  right: 0px;
  left: 0px;
  background-image: linear-gradient(to right, rgb(83, 104, 120), rgb(112, 128, 144), rgb(145, 163, 176));
  height: 300px;
  border: none;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  font-size: 20px;
  flex-direction: row;
}
#div1 {
  height: 150px;
  width: 600px;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  background-color: transparent;
  color: white;
  top: 10px;
  align-items: center;
}

#div2 {
  height: 150px;
  width: 400px;
  display: flex;
  align-items: row;
  background-color: transparent;
  color: white;
  padding-left: 30px;
  line-height: 1.7;
  
}

